import { useState } from "react";
import styles from "../Header/SideNav.scss";

const SideNav = () => {
    const [isOpen, setIsOpen] = useState(true);
    return (
        <div>
            <button
                onClick={() => setIsOpen(!isOpen)}
                className={styles["button"]}
            />
            {isOpen ? (
                <ul>
                    <li>
                        <a href="#about">About Me</a>
                    </li>
                </ul>
            ) : null}
        </div>
    );
};

export default SideNav;
